body .is-layout-constrained .wp-block-blocks-gamestore-recent-news {
	max-width: calc(100% - calc(var(--wp--style--root--padding-left) + var(--wp--style--root--padding-right))) !important;
	// margin-right: calc(var(--wp--style--root--padding-right) * -1) !important;
	// margin-left: calc(var(--wp--style--root--padding-left) * -1) !important;
	margin-inline: auto !important;
	margin-top: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem) !important;
	margin-bottom: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem) !important;
}

.wp-block-blocks-gamestore-recent-news {
	text-align: center;
	position: relative;
	background-size: cover;
	z-index: 1;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(180deg, var(--background-default, #FAFAFA) 0%, var(--background-80, rgba(250, 250, 250, 0.80)) 50.06%, var(--background-default, #FAFAFA) 100%);
		z-index: -1;
	}

	h2 {
		line-height: 1.4;
		background: var(--accent-gradient, linear-gradient(90deg, #3DAFE2 0%, #6F76C0 100%));
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		margin-top: 0;
		margin-bottom: 24px;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
		width: fit-content;
	}

	p {
		max-width: 980px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		margin-top: 24px;
		margin-bottom: 48px;
		color: var(--text-secondary, rgba(14, 13, 15, .7));
	}

	.recent-news {
		display: flex;
		align-items: flex-start;
		gap: 48px;

		.news-item {
			width: calc(33.33% - 32px);
			position: relative;
			overflow: hidden;
			border-radius: 8px;
			background: var(--background-default, #FAFAFA);
			box-shadow: 0px 24px 32px 0px var(--shadow-24, rgba(14, 13, 15, 0.08));
			padding-top: 48px;
			padding-bottom: 48px;
			display: flex;
			flex-direction: column;
			gap: 24px;
			align-items: center;

			&::before {
				content: "";
				width: 100%;
				height: 10px;
				position: absolute;
				top: 0;
				left: 0;
				background: var(--accent-gradient, linear-gradient(90deg, #3dafe2 0%, #6f76c0 100%));
			}

			h3 {
				font-size: 32px;
				font-weight: 700;
				line-height: 1.4;
				background: var(--accent-gradient, linear-gradient(90deg, #3dafe2 0%, #6f76c0 100%));
				background-clip: text;
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
				text-align: center;
				padding-left: 20px;
				padding-right: 20px;
				overflow: hidden;
				display: block;
				max-height: 6.25rem;
				line-height: 3.12rem;
				text-overflow: ellipsis;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}

			.news-thumbnail {
				position: relative;
				aspect-ratio: 16 / 9;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}

				.blur-image {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					filter: blur(24px);
				}

				.original-image {
					position: relative;
					z-index: 1;
				}
			}
		}
		.news-exerpt {
			padding-left: 48px;
			padding-right: 48px;
			color: var(--text-secondary, rgba(14, 13, 15, 0.70));
			line-height: 1.7;
			overflow: hidden;
			display: block;
			max-height: 8.5rem;
			text-overflow: ellipsis;
			-webkit-line-clamp: 4;
			-webkit-box-orient: vertical;
		}

		.read-more {
			display: inline-flex;
			justify-content: center;
			align-items: center;
			gap: 10px;
			background-repeat: 5px;
			border-radius: 5px;
			background: var(--accent-gradient, linear-gradient(90deg, var(--blue-main, #3DAFE2) 0%, var(--purple-main, #6F76C0) 100%));
			box-shadow: 0px 16px 24px 0px var(--blue-48, rgba(61, 175, 226, 0.48));
			padding: 20px 32px;
			font-size: 14px;
			font-style: normal;
			font-weight: 700;
			line-height: 140%;
			letter-spacing: 2.1px;
			text-transform: uppercase;
			text-decoration: none;
			color: #fff;
			transition: .3s ease-in-out background;

			&:hover {
				background: linear-gradient(90deg, var(--blue-main, #6f76c0) 0%,  #575ead 100%);
			}

			&::before {
				content: "";
				width: 24px;
				height: 24px;
				background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="25" height="24" viewBox="0 0 25 24" fill="none"><path d="M16.5 2H8.5C5 2 3.5 4 3.5 7V17C3.5 20 5 22 8.5 22H16.5C20 22 21.5 20 21.5 17V7C21.5 4 20 2 16.5 2ZM8.5 12.25H12.5C12.91 12.25 13.25 12.59 13.25 13C13.25 13.41 12.91 13.75 12.5 13.75H8.5C8.09 13.75 7.75 13.41 7.75 13C7.75 12.59 8.09 12.25 8.5 12.25ZM16.5 17.75H8.5C8.09 17.75 7.75 17.41 7.75 17C7.75 16.59 8.09 16.25 8.5 16.25H16.5C16.91 16.25 17.25 16.59 17.25 17C17.25 17.41 16.91 17.75 16.5 17.75ZM19 9.25H17C15.48 9.25 14.25 8.02 14.25 6.5V4.5C14.25 4.09 14.59 3.75 15 3.75C15.41 3.75 15.75 4.09 15.75 4.5V6.5C15.75 7.19 16.31 7.75 17 7.75H19C19.41 7.75 19.75 8.09 19.75 8.5C19.75 8.91 19.41 9.25 19 9.25Z" fill="white"/></svg>');
			}
		}
	}

	img {
		max-width: 100%;
	}
}